import ButtonDota from "@/components/utils/cards/ButtonDota"
import useAxios from "@/hooks/useAxios"
import axiosInstance from "@/services"
import { Lessons, Sections } from "@/types/Academy"
import { Card, Link, Spinner } from "@heroui/react"
import { useParams } from "next/navigation"
import { useRouter } from "next/router"
import { useEffect, useState } from "react"

const CourseId = () => {
    const params = useParams<{ id: string }>()
    const { data: sections } = useAxios<Sections[]>({ url: `academy/courses/${params.id}/sections`, method: 'get' })
    const [sectionsAll, setSectionsall] = useState<(Sections & { lessons: Lessons[] })[] | null>(null)

    useEffect(() => {
        getLessons()
    }, [sections, params.id])

    const getLessons = async () => {
        if (sections) {
            const sectionLesson = await Promise.all(
                sections.map(async (section) => {
                    const response = await axiosInstance.get<Lessons[]>(`academy/courses/${params.id}/sections/${section.id}/lessons`)
                    const each = response.data
                    return {
                        ...section,
                        lessons: each
                    }
                })
            )
            setSectionsall(sectionLesson)
        }
    }
    console.log("lessons", sectionsAll)

    if (!sectionsAll) return (<Spinner />)
    return (
        <div className="flex flex-col gap-12">
            {sectionsAll.map((section) => (
                <Card className="flex flex-col gap-4 p-4">
                    <div className="flex justify-between items-center">
                        <div className="flex gap-4 items-end">
                            <span className="text-xl">{section?.name || "Sin nombre"}</span>
                            <span className="text-md text-gray-400">{"Hace 5 meses"}</span>
                        </div>
                        <span>{section?.count_lessons} {section?.count_lessons == 1 ? 'Lección' : 'Lecciones'}</span>
                    </div>
                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                        {section.lessons.length > 0 && section.lessons.map((lesson) => (
                            <Card className="p-2 flex flex-col gap-2 ">
                                <div className="relative">
                                    <img
                                        src="/img/academy/bg-academy.jpg"
                                        alt=""
                                        className="rounded-xl"
                                    />
                                    <div className="bg-gray-600 absolute bottom-0 flex items-center right-0 px-2 opacity-70 rounded-br-xl">
                                        <span>4:30</span>
                                    </div>
                                </div>
                                <div className="flex flex-col">
                                    <span className="text-lg">{lesson?.name}</span>
                                    <span className="text-gray-600">Hace 5 meses</span>
                                </div>
                            </Card>
                        ))}
                    </div>
                    <div className="flex justify-end">
                        <Link href={`/academy/course/${params.id}/section/${section.id}`}><ButtonDota classNames="text-white">Ver Lecciones</ButtonDota></Link>
                    </div>
                </Card>
            ))}
        </div>
    )
}

export default CourseId